.hero {
  margin-bottom: 64px;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
  }

  &__header {
    max-width: 765px;
  }

  &__title {
    display: inline;
  }

  &__img {
    background: var(--white);
  }

  &__img--dnone {
    display: none;
  }
}

@media (max-width: 1250px) {
  .hero {
    &__container {
      gap: 0;
    }

    &__header {
      max-width: 1200px;
      display: flex;
      align-items: center;
      flex-direction: column;
      gap: 24px;
    }

    &__title {
      display: block;
      text-align: center;
    }

    &__img {
      display: none;
    }

    &__img--dnone {
      display: block;
    }
  }
}

@media (max-width: 576px) {
  .hero {
    margin-bottom: 34px;

    &__title {
      display: block;
      text-align: start;
    }
  }
}
