.swiper {
  width: 100%;
  height: 100%;

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-flow: column;
    column-gap: 20px;
    overflow: hidden;
  }

  &__slide {
    width: 100%;
    height: 100%;
    padding: 0 37px;
    transform: translateX(0);
    transition: transform 0.3s ease-in-out;
  }

  &__article {
    display: flex;
    flex-direction: column;
    height: 100%;
    row-gap: 25px;
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
  }

  &__title {
    margin-bottom: 5px;
  }

  &__text {
    margin-bottom: 20px;
  }

  &__btn {
    margin-top: auto;
  }
}

@media (max-width: 1250px) {
  .swiper {
    max-width: 830px;
    margin: 0 auto;
  }
}

@media (max-width: 850px) {
  .swiper {
    max-width: 335px;

    &__slide {
      width: 335px;
      padding: 0px 25px;
    }

    &__top {
      margin: 0 auto;
    }

    &__img {
      width: 244px;
      height: 244px;
    }
  }
}

@media (max-width: 576px) {
  .swiper {
    &__article {
      row-gap: 28px;
    }

    &__text {
      font-size: 20px;
      margin-bottom: 22px;
    }
  }
}
