.stages {
  margin-bottom: 200px;

  &__container {
    position: relative;
    overflow: hidden;
  }

  &__header {
    max-width: 722px;
    margin-bottom: 54px;
  }

  &__title {
    display: inline;
  }

  &__block {
    display: inline;
  }

  &__span {
    max-width: 280px;
    width: 100%;
    display: inline-block;
    letter-spacing: 0.9px;
    margin-left: 15px;
    color: var(--blue);
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 3;
    gap: 20px;
  }

  &__item {
    position: relative;
    background: url(../img/backgr-list.svg);
    padding: 20px 21px 20px 72px;
    letter-spacing: 0.8px;

    &::before {
      position: absolute;
      top: 20px;
      left: 20px;
      content: "1";
      border-radius: 60px;
      width: 36px;
      height: 36px;
      background-color: var(--white);
      text-align: center;
      padding: 6px 0;
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 20px;
      line-height: 120%;
      color: var(--grey);
    }
  }

  &__item:nth-child(2)::before {
    content: "2";
  }

  &__item:nth-child(3) {
    grid-row: 2 span;

    &::before {
      content: "3";
    }
  }

  &__item:nth-child(4)::before {
    content: "4";
  }

  &__item:nth-child(5)::before {
    content: "5";
  }

  &__item:nth-child(6)::before {
    content: "6";
  }

  &__item:nth-child(7) {
    grid-column: 2 span;

    &::before {
      content: "7";
    }

    &::after {
      content: url(.);
      position: absolute;
      right: -42px;
      top: -84px;
    }
  }

  &__text {
    max-width: 446px;
    margin: 0;
  }

  &__img {
    position: absolute;
    bottom: 0;
    right: -48px;
    z-index: 1;
  }

  &__block-navigation {
    display: none;
  }
}

@media (max-width: 1250px) {
  .stages {
    &__header {
      z-index: 3;
      position: relative;
      backdrop-filter: blur(3px);
    }

    &__img {
      bottom: auto;
      top: 5px;
    }

    &__text {
      max-width: 100%;
      margin: 0;
    }

    &__list {
      grid-template-columns: repeat(2, 1fr);
    }

    &__item:nth-child(6) {
      grid-column: 2 span;
    }
  }
}

@media (max-width: 576px) {
  .stages {
    margin-bottom: 116px;

    &__header {
      backdrop-filter: blur(0);
      margin-bottom: 148px;
    }

    &__title {
      display: block;
      margin-bottom: 6px;
    }

    &__span {
      max-width: 295px;
      margin-left: 0;
      font-size: 18px;
      letter-spacing: 0.8px;
    }

    &__img {
      top: 27.5%;
      right: 50%;
      max-width: 335px;
      transform: translateX(50%);
    }

    &__list {
      grid-template-columns: repeat(5, 335px);
      grid-template-rows: repeat(2, 1fr);
      row-gap: 0;
      column-gap: 20px;
      overflow: hidden;
      width: 335px;
      margin: 0 auto;
      margin-bottom: 24px;
    }

    &__item {
      padding: 64px 20px 0 72px;
      transition: transform 0.3s ease-in-out;

      &::before {
        top: 64px;
      }
    }

    &__item:nth-child(1) {
      order: 1;
    }

    &__item:nth-child(2) {
      order: 5;
      padding-top: 30px;

      &::before {
        top: 30px;
      }
    }

    &__item:nth-child(3) {
      grid-row: 2 span;
      order: 2;
    }

    &__item:nth-child(4) {
      order: 3;
    }

    &__item:nth-child(5) {
      order: 7;
      padding-top: 30px;

      &::before {
        top: 30px;
      }
    }

    &__item:nth-child(6) {
      grid-column: 1 span;
      grid-row: 2 span;
      order: 3;
    }

    &__item:nth-child(7) {
      grid-column: 1 span;
      grid-row: 2 span;
      order: 4;
    }

    &__block-navigation {
      display: flex;
      width: 100%;
      justify-content: center;
      align-items: center;
      gap: 16px;
      padding: 2px 0;
    }

    &__count {
      display: flex;
      gap: 6px;
    }
  }
}
