.table {
  width: 100%;

  &__column {
    display: grid;
    grid-template-columns: 306px 1fr;
  }

  &__column:not(:last-child) {
    border-bottom: 2px solid #d0d0d0;
  }

  &__column:not(:first-child) .table__line {
    padding-top: 14px;
  }

  &__column:last-child .table__line {
    padding-bottom: 0;
  }

  &__line {
    text-align: start;
    padding-bottom: 16px;
  }

  &__line:last-child {
    padding-left: 16px;
  }

  &__line:not(:last-child) {
    border-right: 2px solid #d0d0d0;
  }

  &__del {
    margin-right: 12px;
    text-decoration: line-through solid var(--red);
  }
}

@media (max-width: 576px) {
  .table {
    &__column {
      grid-template-columns: repeat(2, 1fr);
      row-gap: 11px;
    }

    &__column:not(:last-child) {
      border-bottom: 2px solid #d0d0d0;
      padding-bottom: 14px;
    }

    &__column:not(:first-child) .table__line {
      padding-top: 0;
    }

    &__column:not(:first-child) {
      padding-top: 16px;
    }

    &__column:nth-child(4) .table__line {
      grid-column: 1 span;
    }

    &__line {
      padding: 0;
      grid-column: 2 span;
      font-size: 20px;
    }

    &__line:last-child {
      padding-left: 0;
    }

    &__line:not(:last-child) {
      border-right: none;
    }
  }
}
