html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  min-width: 320px;
}

.img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.t-60 {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 60px;
  line-height: 110%;
  text-transform: uppercase;
  color: var(--gray);
  margin: 0;
}

.t-54 {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 54px;
  line-height: 120%;
  text-transform: uppercase;
  color: var(--gray);
  margin: 0;
}

.t-36 {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 36px;
  line-height: 135%;
  text-transform: uppercase;
  color: var(--gray);
  margin: 0;
}

.t-24 {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 24px;
  line-height: 120%;
  color: var(--gray);
  margin: 0;
}

.span {
  font-weight: 700;
  color: var(--red);
  letter-spacing: 0.5px;
}

.t-22 {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 22px;
  line-height: 110%;
  text-transform: uppercase;
  text-align: right;
  color: var(--white);
  margin: 0;
}

.t-20 {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 20px;
  line-height: 120%;
  color: var(--gray);
  margin: 0;
}

.blue {
  color: var(--blue);
}

.t-regular {
  font-weight: 400;
  letter-spacing: 0.5px;
}

.t-medium {
  font-weight: 500;
  color: var(--gray);
}

.opacity {
  opacity: 0.7;
}

.t-18 {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 130%;
  text-align: center;
  color: var(--gray);
  margin: 0;
}

.t-16 {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: var(--gray);
  opacity: 0.6;
  margin: 0;
}

.link-black {
  text-decoration: none;
  border-radius: 66px;
  padding: 20px;
  background: var(--black);
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  text-align: center;
  color: var(--white);
  transition: color 0.3s ease-in-out, background 0.3s ease-in-out,
    outline 0.3s ease-in-out, opacity 0.3s ease-in-out;

  &:hover {
    background: var(--yellow);
    color: var(--black);
  }

  &:focus {
    outline: 2px solid var(--yellow);
  }

  &:active {
    outline: none;
    opacity: 0.4;
  }
}

.link-transparent {
  text-decoration: none;
  border: 2px solid var(--black);
  border-radius: 66px;
  padding: 20px;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  text-align: center;
  color: var(--black);
  transition: color 0.3s ease-in-out, background 0.3s ease-in-out,
    border 0.3s ease-in-out, outline 0.3s ease-in-out, opacity 0.3s ease-in-out;

  &:hover {
    background: var(--black);
    color: var(--white);
    border: 2px solid var(--black);
  }

  &:focus {
    outline: 2px solid var(--yellow);
  }

  &:active {
    outline: none;
    opacity: 0.4;
  }
}

.btn-slide {
  width: 44px;
  height: 44px;
  background: var(--black);
  border-radius: 100%;
  color: var(--white);
  padding: 12px;
  transition: background 0.3s ease-in-out, outline 0.3s ease-in-out,
    opacity 0.3s ease-in-out;

  &:hover {
    background: var(--yellow);
  }

  &:focus {
    outline: 2px solid var(--yellow);
  }

  &:active {
    outline: none;
    opacity: 0.4;
  }

  &::before {
    content: url(../img/next.svg);
    width: 12px;
    height: 6px;
  }
}

.btn-next {
  transform: rotate(180deg);
}

.btn-dis {
  background: #d6d6d6;
  cursor: not-allowed;

  &:hover {
    background: #d6d6d6;
  }

  &:focus {
    outline: none;
  }

  &:active {
    opacity: 1;
  }
}

.btn-blue {
  border: 1px solid var(--blue);
  border-radius: 62px;
  padding: 6px 12px;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  color: var(--blue);
  background: var(--white);
  transition: color 0.3s ease-in-out, background 0.3s ease-in-out,
    border 0.3s ease-in-out, outline 0.3s ease-in-out, opacity 0.3s ease-in-out;

  &:hover {
    background: var(--blue);
    color: var(--white);
    border: 1px solid var(--blue);
  }

  &:focus {
    outline: 2px solid var(--yellow);
    color: var(--yellow);
  }

  &:active {
    outline: none;
    opacity: 0.4;
  }
}

.logo {
  display: block;
  width: 245px;
  height: 36px;
  transition: outline 0.3s ease-in-out, opacity 0.3s ease-in-out;

  &:focus {
    outline: 2px solid var(--yellow);
  }

  &:hover {
    .logo__svg {
      fill: var(--gray);
    }
  }

  &:active {
    opacity: 0.4;
    outline: none;
  }

  &__svg {
    fill: #595653;
    width: 245px;
    height: 36px;
    transition: fill 0.3s ease-in-out;
  }
}

.btn-nav {
  width: 10px;
  height: 10px;
  background: #d9d9d9;
  border-radius: 100%;
  transition: background 0.3s ease-in-out, outline 0.3s ease-in-out,
    opacity 0.3s ease-in-out;

  &:hover {
    background: var(--yellow);
    outline: 2px solid var(--yellow);
  }

  &:focus {
    background: var(--black);
    outline: 2px solid var(--black);
  }

  &:active {
    opacity: 0.4;
    outline: none;
  }
}

.btn-nav--active {
  background: var(--black);
  cursor: not-allowed;
}

@media (max-width: 1250px) {
  .link-transparent {
    background: #fff;
    opacity: 0.9;
    border: 2px solid var(--white);
  }
}

@media (max-width: 576px) {
  .t-60 {
    font-size: 36px;
  }

  .t-54 {
    font-size: 36px;
  }

  .t-36 {
    font-size: 28px;
  }

  .t-22 {
    font-size: 16px;
  }

  .t-20 {
    font-size: 18px;
  }

  .btn-slide {
    width: 36px;
    height: 36px;
    padding: 8px;
  }

  .logo {
    display: block;
    width: 206px;
    height: 30px;

    &__svg {
      fill: #595653;
      width: 206px;
      height: 30px;
    }
  }
}
