.ticker {
  position: relative;
  overflow: hidden;
  display: flex;
  bottom: 0;
  background: var(--red);
  height: 60px;
  box-sizing: border-box;
  z-index: 5;

  &__list {
    width: fit-content;
    display: flex;
    padding: 0;
    margin: 0;
    padding: 18px 0;
  }

  &__list[hidden]:last-child {
    display: none;
  }

  &__list--right {
    animation: ticker-right 15s linear infinite;
  }

  &__list--left {
    animation: ticker-left 15s linear infinite;
  }

  &__item {
    text-align: start;
  }

  &__item:first-child {
    width: 805px;
  }

  &__item:nth-child(2) {
    width: 881px;
  }

  &__item:last-child {
    width: 666px;
  }
}

@keyframes ticker-right {
  0% {
    transform: translate3d(1%, 0, 0);
  }
  100% {
    transform: translate3d(100%, 0, 0);
  }
}

@keyframes ticker-left {
  0% {
    transform: translate3d(-200%, 0, 0);
  }
  100% {
    transform: translate3d(-99%, 0, 0);
  }
}

@media (max-width: 576px) {
  .ticker {
    height: 38px;

    &__list {
      padding: 10px 0;
    }
  }
}
