.participants {
  margin-bottom: 142px;

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 60px;
  }

  &__block-btn {
    display: flex;
    align-items: center;
    column-gap: 16px;
  }
}

@media (max-width: 1400px) {
}

@media (max-width: 1250px) {
}

@media (max-width: 850px) {
  .participants {
    position: relative;
    margin-bottom: 100px;
    padding-bottom: 77px;

    &__header {
      margin-bottom: 34px;
    }

    &__block-btn {
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }

    &__block-btn {
      bottom: 0px;
      column-gap: 13px;
    }
  }
}

