.info {
  margin-bottom: 198px;

  &__container {
    display: flex;
    justify-content: space-between;
    gap: 24px;
  }

  &__conteiner-image {
    max-width: 474px;
    width: 100%;
    position: relative;
  }

  &__background {
    bottom: 53px;
    left: 22px;
    background: url(../img/bacgr2.jpg);
    max-width: 406px;
    height: 438px;
    z-index: 1;
  }

  &__money {
    top: 17px;
    right: 0;
    background: url(../img/money.jpg);
    max-width: 121px;
    height: 240px;
    z-index: 2;
  }

  &__boom-big {
    background: url(../img/big-boom.svg);
    max-width: 600px;
    height: 343px;
    top: -34px;
    left: -30px;
    z-index: 3;
    animation: boom-big 2s linear infinite alternate-reverse;

    @keyframes boom-big {
      0% {
        opacity: 1;
      }

      100% {
        opacity: 0;
      }
    }
  }

  &__people {
    background: url(../img/people.png);
    max-width: 325px;
    height: 330px;
    top: 16%;
    left: 16%;
    z-index: 4;
  }
  &__boom {
    background: url(../img/boom.svg);
    max-width: 245px;
    height: 210px;
    bottom: 25px;
    left: -15px;
    z-index: 5;
    animation: boom 5s linear infinite alternate-reverse;
  }

  @keyframes boom {
    0% {
      opacity: 1;
    }
    10% {
      opacity: 0;
    }
    90% {
      opacity: 0;
    }
    100% {
      opacity: 0;
    }
  }

  &__horse {
    background: url(../img/horse2.png);
    max-width: 155px;
    height: 170px;
    bottom: 6px;
    left: 62px;
    z-index: 6;
    animation: horse2 5s linear infinite alternate-reverse;
  }

  @keyframes horse2 {
    0% {
      transform: rotate(0);
    }
    100% {
      transform: rotate(33deg);
    }
  }

  &__hand {
    background: url(../img/hand.png);
    max-width: 274px;
    height: 206px;
    bottom: -7px;
    right: 7px;
    z-index: 6;
    animation: hand 5s linear infinite alternate-reverse;
  }

  @keyframes hand {
    0% {
      transform: translate(-10px, 0) rotate(15deg);
    }
    100% {
      transform: translate(0, 0) rotate(-20deg);
    }
  }

  &__content {
    max-width: 704px;
  }

  &__title {
    margin-bottom: 54px;
  }

  &__table {
    margin-bottom: 48px;
  }

  &__text {
    letter-spacing: 0.6px;
    color: var(--blue);
  }
}

.background {
  background-repeat: no-repeat;
  position: absolute;
  width: 100%;
}

@media (max-width: 1250px) {
  .info {
    &__container {
      position: relative;
      min-height: 554px;
    }

    &__content {
      position: absolute;
      top: 0;
      right: 0;
      backdrop-filter: blur(1px);
      background: #ffffffa3;
      padding: 20px;
      z-index: 7;
    }
  }
}

@media (max-width: 576px) {
  .info {
    margin-bottom: 118px;

    &__container {
      flex-direction: column;
    }

    &__conteiner-image {
      max-width: 335px;
      height: 402px;
    }

    &__content {
      position: relative;
      padding: 0;
    }

    &__background {
      max-width: 287px;
      height: 312px;
      bottom: 45px;
      left: 15px;
    }

    &__money {
      top: 36px;
      right: 0;
      max-width: 86px;
      height: 171px;
      background-size: contain;
    }

    &__boom-big {
      background: url(../img/big-boom.svg);
      max-width: 335px;
      height: 243px;
      top: 13px;
      left: 0;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &__people {
      background: url(../img/people.png);
      max-width: 236px;
      height: 240px;
      top: 21%;
      left: 16%;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &__boom {
      background: url(../img/boom.svg);
      max-width: 168px;
      height: 167px;
      bottom: 2px;
      left: -7px;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &__horse {
      background: url(../img/horse2.png);
      max-width: 115px;
      height: 132px;
      bottom: 0;
      left: 43px;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &__hand {
      background: url(../img/hand.png);
      max-width: 196px;
      height: 202px;
      bottom: -56px;
      right: 0;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &__title {
      margin-bottom: 48px;
    }

    &__table {
      margin-bottom: 40px;
    }

    &__text {
      font-size: 18px;
    }
  }
}
