.header {
  background: url(../img/main.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  margin-bottom: 80px;
  overflow: hidden;

  &__city {
    position: absolute;
    width: 100%;
    object-fit: cover;
    max-width: 1626px;
    height: 452px;
    bottom: 0;
    right: 6.5%;
    z-index: 4;
  }

  &__sun {
    background: url(../img/sun.svg);
    max-width: 661px;
    height: 661px;
    top: 0;
    right: 16%;
    z-index: 1;
  }

  &__letters {
    background: url(../img/let.svg);
    max-width: 634px;
    height: 634px;
    top: 10px;
    right: 16.7%;
    overflow: hidden;
    z-index: 2;
    animation: rotate 30s linear infinite;
  }

  @keyframes rotate {
    0% {
      transform: rotate(0);
    }
    100% {
      transform: rotate(359deg);
    }
  }

  &__king {
    background: url(../img/king.png);
    max-width: 146px;
    height: 266px;
    bottom: 327px;
    right: 22.4%;
    z-index: 3;
    animation: figures 3s linear infinite alternate-reverse;
  }

  @keyframes figures {
    0% {
      transform: rotate(10deg);
    }
    100% {
      transform: rotate(-20deg);
    }
  }

  &__queen {
    background: url(../img/queen.png);
    max-width: 131px;
    height: 266px;
    bottom: 222px;
    right: 40.6%;
    z-index: 3;
    animation: figures2 3s linear infinite alternate-reverse;
  }

  @keyframes figures2 {
    0% {
      transform: rotate(-10deg);
    }
    100% {
      transform: rotate(15deg);
    }
  }

  &__pawn {
    background: url(../img/pawn.png);
    max-width: 83px;
    height: 123px;
    z-index: 4;
    bottom: 75px;
    right: 16.5%;
    animation: pawn 10s linear infinite;
  }

  @keyframes pawn {
    0% {
      transform: translateY(0);
    }
    10% {
      transform: translateY(-60px);
    }
    20% {
      transform: translateY(-60px) rotate(10deg);
    }
    30% {
      transform: translate(-60px, -60px);
    }
    40% {
      transform: translate(-60px, 0px);
    }
    50% {
      transform: translate(-60px, -60px);
    }
    60% {
      transform: translate(-60px, -60px) rotate(10deg);
    }
    70% {
      transform: translate(-120px, -60px);
    }
    80% {
      transform: translate(-120px, 0);
    }
    100% {
      transform: rotateX(0);
    }
  }

  &__horse {
    background: url(../img/horse.png);
    max-width: 197px;
    height: 218px;
    z-index: 4;
    bottom: 58px;
    right: 31.5%;
    animation: horse 7s linear infinite;
  }

  @keyframes horse {
    0% {
      transform: translate(318%, 0);
      opacity: 0.4;
    }
    20% {
      transform: translate(0, 0);
      opacity: 1;
    }

    60% {
      transform: translate(0, 0);
      opacity: 1;
    }

    90% {
      transform: translate(-318%, 0px);
    }

    100% {
      transform: translate(-600%, 0px);
      opacity: 0.4;
    }
  }

  &__container {
    padding-top: 26px;
    padding-bottom: 130px;
  }

  &__logo-link {
    margin-bottom: 62px;
  }

  &__content {
    max-width: 542px;
    position: relative;
    z-index: 5;
  }

  &__title {
    margin-bottom: 24px;
  }

  &__span {
    display: block;
  }

  &__span--center {
    text-align: center;
  }

  &__span--right {
    text-align: right;
  }

  &__text {
    letter-spacing: 0.6px;
    margin-bottom: 40px;
  }

  &__block-btn {
    display: flex;
    gap: 16px;
  }

  &__link {
    max-width: 263px;
    padding: 13px 50px;
  }
}

@media (max-width: 1600px) {
  .header {
    &__city {
      right: 0;
    }

    &__sun {
      top: -35px;
      right: 2.5%;
    }

    &__letters {
      top: -25px;
      right: 3.5%;
    }

    &__king {
      right: 11.4%;
    }
    &__queen {
      right: 37%;
    }

    &__pawn {
      bottom: 80px;
      right: 3.2%;
    }

    &__horse {
      right: 24.5%;
    }
  }
}

@media (max-width: 1250px) {
  .header {
    &__sun {
      right: 0;
    }

    &__letters {
      right: 1%;
    }
  }
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .header {
    &__sun {
      right: 0;
      max-width: 400px;
      height: 400px;
      top: 80px;
    }

    &__letters {
      max-width: 380px;
      height: 380px;
      right: 1%;
      top: 90px;
      background-size: contain;
    }

    &__king {
      max-width: 127px;
      height: 239px;
      right: 0%;
    }

    &__queen {
      right: 28%;
    }
  }
}

@media (max-width: 576px) {
  .header {
    margin-bottom: 48px;

    &__container {
      padding-top: 20px;
      padding-bottom: 288px;
    }

    &__logo-link {
      margin-bottom: 42px;
    }

    &__text {
      text-align: start;
      margin-bottom: 30px;
    }

    &__block-btn {
      flex-wrap: wrap;
      justify-content: center;
      gap: 14px;
    }

    &__link {
      max-width: 335px;
      width: 100%;
    }

    &__city {
      height: 243px;
      object-fit: fill;
    }

    &__sun {
      right: 48px;
      max-width: 297px;
      height: 297px;
      top: auto;
      bottom: 106px;
    }

    &__letters {
      right: 57px;
      max-width: 280px;
      height: 280px;
      top: auto;
      bottom: 110px;
    }

    &__king {
      max-width: 72px;
      height: 104px;
      right: 21%;
      bottom: 210px;
      background: url(../img/king375.png);
    }

    &__pawn {
      display: none;
    }

    &__queen {
      max-width: 72px;
      right: 64%;
      bottom: 5px;
      background: url(../img/queen375.png);
    }

    &__horse {
      max-width: 90px;
      height: 140px;
      right: 37.5%;
      bottom: 13px;
      background: url(../img/horse375.png);
    }
  }
}
